.buttonsWrapper {
  padding: 15px;
  display: flex;
  justify-content: space-around;
  flex-basis: 60px;
  .button {
    width: 140px;
    height: 45px;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: 3px solid #2e7de5;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    &:hover {
      background-color: #2e7de5;
      box-shadow: 0px 15px 20px rgba(21, 87, 231, 0.726);
      color: #fff;
      transform: translateY(-7px);
    }
  }
}
